import { Component, OnInit, Renderer2, AfterViewInit } from '@angular/core';
import { LangService } from './shared/lang.service';
import { environment } from '../environments/environment';
import { Injectable } from '@angular/core';
import {DataService} from './data/data.service'
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html'
})

@Injectable()
export class AppComponent implements OnInit, AfterViewInit {
  isMultiColorActive = environment.isMultiColorActive;
  constructor(private dataService: DataService,private langService: LangService, private renderer: Renderer2) {

  }

  ngOnInit() {
    const docname = "Perfil del exalumno"
    if(!localStorage.getItem("email")){

      localStorage.setItem("email","");
    }
    this.langService.init();


    this.dataService.get_doc(docname,localStorage.getItem("email")).subscribe(data => {
      let resp = data["data"]
      DataService.ENV_VARS.UserData = resp
    })
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.renderer.addClass(document.body, 'show');
    }, 1000);
    setTimeout(() => {
      this.renderer.addClass(document.body, 'default-transition');
    }, 1500);
  }
}
